'use client';

import { useState } from 'react';
import Layout from '@/components/Layout';
import { exportApi } from '@/lib/api';
import toast from 'react-hot-toast';
import { ArrowDownTrayIcon, DocumentArrowDownIcon } from '@heroicons/react/24/outline';

export default function ExportPage() {
  const [exporting, setExporting] = useState(false);

  const handleExcelExport = async () => {
    setExporting(true);
    try {
      await exportApi.excel();
      toast.success('Excel file downloaded successfully!');
    } catch (error) {
      toast.error('Failed to export Excel file');
      console.error(error);
    } finally {
      setExporting(false);
    }
  };

  const handleSummaryExport = async () => {
    setExporting(true);
    try {
      const summary = await exportApi.summary();
      const blob = new Blob([summary.summary], { type: 'text/markdown' });
      const url = window.URL.createObjectURL(blob);
      const link = document.createElement('a');
      link.href = url;
      link.download = `summary_report_${new Date().toISOString().split('T')[0]}.md`;
      document.body.appendChild(link);
      link.click();
      link.remove();
      window.URL.revokeObjectURL(url);
      toast.success('Summary report downloaded successfully!');
    } catch (error) {
      toast.error('Failed to export summary');
      console.error(error);
    } finally {
      setExporting(false);
    }
  };

  return (
    <Layout>
      <div className="space-y-6">
        <h1 className="text-3xl font-bold text-gray-900">Export Data</h1>

        <div className="grid grid-cols-1 gap-6 sm:grid-cols-2">
          {/* Excel Export */}
          <div className="bg-white shadow rounded-lg p-8">
            <div className="text-center">
              <div className="mx-auto flex items-center justify-center h-12 w-12 rounded-full bg-green-100">
                <DocumentArrowDownIcon className="h-6 w-6 text-green-600" />
              </div>
              <h2 className="mt-4 text-lg font-medium text-gray-900">Export to Excel</h2>
              <p className="mt-2 text-sm text-gray-500">
                Download all your data in Excel format with multiple sheets including transactions, vendors, categories, and anomalies.
              </p>
              <button
                onClick={handleExcelExport}
                disabled={exporting}
                className="mt-6 inline-flex items-center px-6 py-3 border border-transparent text-base font-medium rounded-md shadow-sm text-white bg-green-600 hover:bg-green-700 disabled:opacity-50"
              >
                <ArrowDownTrayIcon className="h-5 w-5 mr-2" />
                {exporting ? 'Exporting...' : 'Download Excel'}
              </button>
            </div>
          </div>

          {/* Summary Export */}
          <div className="bg-white shadow rounded-lg p-8">
            <div className="text-center">
              <div className="mx-auto flex items-center justify-center h-12 w-12 rounded-full bg-blue-100">
                <DocumentArrowDownIcon className="h-6 w-6 text-blue-600" />
              </div>
              <h2 className="mt-4 text-lg font-medium text-gray-900">Export Summary Report</h2>
              <p className="mt-2 text-sm text-gray-500">
                Download a markdown-formatted summary report with key statistics and insights.
              </p>
              <button
                onClick={handleSummaryExport}
                disabled={exporting}
                className="mt-6 inline-flex items-center px-6 py-3 border border-transparent text-base font-medium rounded-md shadow-sm text-white bg-blue-600 hover:bg-blue-700 disabled:opacity-50"
              >
                <ArrowDownTrayIcon className="h-5 w-5 mr-2" />
                {exporting ? 'Exporting...' : 'Download Summary'}
              </button>
            </div>
          </div>
        </div>

        {/* Export Information */}
        <div className="bg-blue-50 border border-blue-200 rounded-lg p-6">
          <h3 className="text-sm font-medium text-blue-900 mb-2">Export Information</h3>
          <ul className="list-disc list-inside text-sm text-blue-800 space-y-1">
            <li>Excel exports include: Transactions, Vendor Stats, Category Breakdown, Anomalies, and Documents</li>
            <li>Summary reports are in Markdown format and can be opened in any text editor</li>
            <li>All exports are generated on-demand with the latest data</li>
          </ul>
        </div>
      </div>
    </Layout>
  );
}

